import Area from '@/components/Area';
import { Environment, GizmoHelper, GizmoViewport, OrbitControls, Shadow, useGLTF } from '@react-three/drei';
import { Canvas, extend, ThreeElement, useFrame } from '@react-three/fiber';
import * as React from 'react';
import gltf from './gears-transformed.glb';
import * as THREE from 'three/webgpu';
import * as TSL from 'three/tsl';
import { Leva, useControls } from 'leva';
import { easing } from 'maath';

// extend({ MeshPhysicalNodeMaterial: THREE.MeshPhysicalNodeMaterial });

declare global {
  namespace React {
    namespace JSX {
      interface IntrinsicElements {
        meshPhysicalNodeMaterial: ThreeElement<typeof THREE.MeshPhysicalNodeMaterial>;
      }
    }
  }
}

function Model(props: React.ComponentPropsWithoutRef<'group'>) {
  const { nodes, materials } = useGLTF(gltf);
  const { start, arc, color } = useControls({
    start: { value: 1.75, min: 0, max: Math.PI },
    arc: { value: 1.25, min: 0, max: Math.PI },
    color: '#0096ff',
  });

  const { output, castShadow, sliceStart, sliceArc, sliceColor } = React.useMemo(() => {
    const sliceStart = TSL.uniform(1.75);
    const sliceArc = TSL.uniform(1.25);
    const sliceColor = TSL.uniform(TSL.color('#0096ff'));

    const inAngle = TSL.Fn<[position: THREE.Node, angleStart: number, angleArc: number]>(
      ([position, angleStart, angleArc]) => {
        const angle = TSL.atan(position.y, position.x)
          .sub(angleStart)
          .mod(TSL.float(Math.PI * 2))
          .toVar();
        return angle.greaterThan(0).and(angle.lessThan(angleArc));
      }
    );

    const output = TSL.Fn(() => {
      inAngle(TSL.positionLocal.xy, sliceStart, sliceArc).discard();
      const finaleOutput = TSL.output;
      TSL.If(TSL.frontFacing.not(), () => void finaleOutput.assign(TSL.vec4(sliceColor, 1)));
      return finaleOutput;
    })();

    const castShadow = TSL.Fn(() => {
      inAngle(TSL.positionLocal.xy, sliceStart, sliceArc).discard();
      return TSL.vec4(0, 0, 0, 1);
    })();

    return { output, castShadow, sliceStart, sliceArc, sliceColor };
  }, []);

  useFrame((_, delta) => {
    easing.damp(sliceStart, 'value', start, 0.1, delta);
    easing.damp(sliceArc, 'value', arc, 0.1, delta);
    easing.dampC(sliceColor.value, color, 0.1, delta);
  });

  // console.log('Model', { nodes, materials });
  return (
    <group {...props} dispose={null}>
      <mesh castShadow receiveShadow geometry={(nodes.outerHull as THREE.Mesh).geometry} scale={3.714}>
        <meshPhysicalNodeMaterial
          outputNode={output}
          // emissiveNode={output}
          // castShadowNode={castShadow}
          // aoMap={(materials.metal as THREE.MeshStandardMaterial).aoMap}
          color="#777"
          // aoMapIntensity={2}
          // metalness={0.6}
          // roughness={0.25}
          side={THREE.DoubleSide}
        />
      </mesh>
      <mesh castShadow receiveShadow geometry={(nodes.axle as THREE.Mesh).geometry} material={materials.metal} />
      <mesh
        castShadow
        receiveShadow
        geometry={(nodes.gears as THREE.Mesh).geometry}
        material={materials.metal}
        material-aoMapIntensity={3}
      />
    </group>
  );
}

export type Props = {};

extend(THREE as any);

function App01(props: Props) {
  const {} = props;
  return (
    <Area style={{ position: 'relative' }}>
      {() => (
        <>
          <Canvas
            shadows
            camera={{ position: [-5, 5, 5], fov: 15 }}
            gl={async (props) => {
              // https://r3f.docs.pmnd.rs/api/canvas#webgpu
              const renderer = new THREE.WebGPURenderer(props as any);
              renderer['capabilities'] = {
                getMaxAnisotropy() {
                  return 0;
                },
              };

              await renderer.init();
              // renderer.physicallyCorrectLights = true;
              // renderer.outputColorSpace = THREE.SRGBColorSpace;
              // renderer.toneMapping = THREE.ACESFilmicToneMapping;
              // renderer.toneMappingExposure = 1.0;
              return renderer;
            }}
          >
            <color attach="background" args={['#eee']} />
            <fog attach="fog" args={['#202025', 8, 9.25]} />
            <OrbitControls
              enableZoom={false}
              enablePan={false}
              minPolarAngle={Math.PI / 3}
              maxPolarAngle={Math.PI / 3}
            />
            <spotLight
              castShadow
              decay={0}
              angle={0.1}
              penumbra={1}
              intensity={Math.PI}
              position={[5, 5, 5]}
              shadow-mapSize={1024}
            />
            <spotLight
              castShadow
              decay={0}
              angle={0.1}
              penumbra={1}
              intensity={Math.PI}
              position={[-5, 15, 5]}
              shadow-mapSize={1024}
            />

            <Model position={[0, 0.025, 0]} scale={0.2} />

            <Shadow position={[0, -0.75, 0]} scale={[7, 5, 1]} opacity={1} colorStop={0.02} />
            <Environment preset="warehouse" environmentIntensity={0.5} />
            <axesHelper />
            <GizmoHelper
              alignment="bottom-right" // widget alignment within scene
              margin={[80, 80]} // widget margins (X, Y)
            >
              <GizmoViewport axisColors={['red', 'green', 'blue']} labelColor="black" />
            </GizmoHelper>
          </Canvas>
          <div style={{ position: 'absolute', top: 0, left: 0, zIndex: 1, width: '250px', height: '100px' }}>
            <Leva fill titleBar={{ drag: false }} />
          </div>
        </>
      )}
    </Area>
  );
}

export default React.memo(App01);
